import React from 'react'
import { Field, reduxForm } from 'redux-form'

const SimpleForm = ({ handleSubmit, pristine, reset, submitting, closeModal }) => {
  return (
    <form onSubmit={handleSubmit}>
      <div>
        <label>Name</label>
        <div>
          <Field name="name" component="input" type="text" placeholder="Name"/>
        </div>
      </div>
      <div>
        <label>Link to img</label>
        <div>
          <Field name="src" component="input" type="text" placeholder="Link to img"/>
        </div>
      </div>
      <div>
        <label>Count</label>
        <div>
          <Field name="count" component="input" type="text" placeholder="Count"/>
        </div>
      </div>
      <div>
        <label>Size</label>
        <div>
          <Field name="size" component="input" type="text" placeholder="Size"/>
        </div>
      </div>
      <div>
        <label>Weight</label>
        <div>
        <Field name="weight" component="input" type="text" placeholder="Weight"/>
        </div>
      </div>
      <div>
        <label>Comments</label>
        <div>
        <Field name="comments" component="input" type="text" placeholder="Comments"/>
        </div>
      </div>
      <div>
        <button type="submit" disabled={pristine || submitting}>Submit</button>
        <button type="button" disabled={pristine || submitting} onClick={reset}>Clear Values</button>
        <button onClick={closeModal}>Close</button>
      </div>
    </form>
  )
}

let ShoItemReduxForm = reduxForm({
  form: 'add-shop-item'})(SimpleForm)

export default ShoItemReduxForm