import React, { useState } from 'react'
import Item from './Item/Item'
import ModalWindow from '../ModalWindow/AddModalWindow'
import s from './ShopItems.module.css'

const ShopItems = (props) => {
	const [value, setValue] = useState('')

	let onSubmit = (formData) => {
		props.addNewItem(formData)
	}

	let filteredGoods = props.goods.filter((a) => {
		return a.name.toLocaleLowerCase().includes(value.toLocaleLowerCase())
	})

	return (
		<div className={s.body}>
			<span>
				<select
					onChange={(e) => {
						props.sortItems(e.target.value)
					}}
				>
					<option value='start_date'>Start Date</option>
					<option value='name'>Name</option>
					<option value='count'>Count</option>
				</select>
			</span>
			<span >
				<form>
					<input
						type='text'
						placeholder='input name'
						onChange={(event) => setValue(event.target.value)}
					/>
				</form>
			</span>

			{filteredGoods.map((i) => (
				<Item
					key={i.id}
					id={i.id}
					src={i.src}
					name={i.name}
					// count={i.count}
					// size={i.size}
					// weight={i.weight}
					// comments={i.comments}
					deleteItem={props.deleteItem}
				/>
			))}
			<ModalWindow onSubmit={onSubmit} />
		</div>
	)
}

export default ShopItems
